import { createWebHistory, createRouter } from 'vue-router'

import Home from '@/views/mobile/home/Home.vue'
import Account from '@/views/mobile/account/Profile.vue'
import MyItem from '@/views/mobile/account/MyItem.vue'
import Messages from '@/views/mobile/message/Messages.vue'
import Search from '@/views/mobile/search/Search.vue'
import Detail from '@/views/mobile/search/Detail.vue'
import MyTransaction from '@/views/mobile/transaction/MyTransaction.vue'
import Login from '@/views/mobile/auth/Login.vue'
import LoginForm from '@/views/mobile/auth/LoginForm.vue'
import SignUpForm from '@/views/mobile/auth/SignUpForm.vue'

const routes = [
    {
        path: "/",
        redirect: "/home"
    },
    {
        path: "/home",
        name: "Beranda",
        component: Home
    },
    {
        path: "/login",
        name: "Masuk",
        component: Login
    },
    {
        path: "/login_form",
        name: "Masuk",
        component: LoginForm
    },
    {
        path: "/sign_up",
        name: "Daftar",
        component: SignUpForm
    },
    {
        path: "/account",
        name: "Akun",
        component: Account
    },
    {
        path: "/account/my_item",
        name: "Item Saya",
        component: MyItem
    },
    {
        path: "/search",
        name: "Pencarian",
        component: Search
    },
    {
        path: "/detail",
        name: "Detail Barang",
        component: Detail
    },
    {
        path: "/messages",
        name: "Pesan",
        component: Messages
    },
    {
        path: "/transaction",
        name: "Transaksi",
        component: MyTransaction
    },
]

const router = createRouter({
    history: createWebHistory(),
    routes
})

export default router;